<template>
    <div>
        <el-card class="box-card">
            <el-row>
                <el-button size="small" type="primary" @click="dataForm"></el-button>
            </el-row>
            <div class="space"></div>
            <!--  -->
            <el-form :inline="true" :model="search">
                <el-form-item label="">
                    <el-input size="small" v-model="search.name"></el-input>
                </el-form-item>
                <el-form-item label="" v-if="false">
                    <el-select size="small" v-model="search.status" placeholder="">
                        <el-option label="" value=""></el-option>
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="0"></el-option>
                    </el-select>
                </el-form-item>
                <div></div>
                <el-form-item>
                    <el-button size="small" type="primary" @click="list"></el-button>
                    <el-button size="small" @click="reset"></el-button>
                </el-form-item>
            </el-form>
        </el-card>
        <div class="space"></div>
        <el-card>
            <!--  -->
            <el-table :data="tableList" style="width: 100%">
                <el-table-column label="" type="index" align="center">
                    <template slot-scope="scope">
                    {{ (search.pageNo-1)*search.pageSize + scope.$index + 1 }}
                    </template>
                </el-table-column>
                <el-table-column prop="name" label="" align="center"></el-table-column>
                <el-table-column prop="status" label="" align="center" v-if="false">
                    <template slot-scope="scope">
                    {{scope.row.status==1?"":""}}
                    </template>
                </el-table-column>
                <el-table-column prop="ctime" label="" align="center">
                    <template slot-scope="scope">
                        <span v-if="scope.row.ctime">{{new Date(scope.row.ctime).Format("yyyy-MM-dd hh:mm:ss")}}</span>
                    </template>
                </el-table-column>
                <el-table-column label="" align="center" width="220">
                    <template slot-scope="scope">
                    <el-button v-if="scope.row.status==1" size="mini" @click="dataForm(scope.$index, scope.row)"></el-button>
                    <el-button v-if="false&&scope.row.status==1" size="mini" @click="modifyData(scope.row.id, 0)" type="danger"></el-button>
                    <el-button v-if="false&&scope.row.status==0" size="mini" @click="modifyData(scope.row.id, 1)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="space"></div>
            <!--  -->
            <el-pagination :small="false" background layout="total, prev, pager, next, jumper"
                @size-change="sizeChange" @current-change="currentChange"
                :current-page="search.pageNo" :page-size="search.pageSize"
                :total="pagination.total">
            </el-pagination>
        </el-card>
        <el-dialog title="" width="30%" :visible.sync="form.show">
            <el-form label-width="100px">
                <el-form-item label="">
                    <el-input size="small" v-model="form.name"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
                <el-button size="small" @click="form.show=false"> </el-button>
                <el-button size="small" type="primary" @click="saveForm"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            search: {
                pageNo: 1,
                pageSize: 10,
                name: '',
                status: ''
            },
            pagination: { // 
                total: 0
            },
            tableList: [],

            form: {
                show: false, // 
                id: '',
                name: ''
            }
        }
    },
    created() {
        this.list();
    },
    methods: {
        currentChange(pageNo) {
            // pageNo
            this.list(event, pageNo);
        },
        sizeChange(pageSize) {
            // pageNo
            this.list(event, null, pageSize);
        },
        // page = {"pageCount":, "count":}
        initPagination(page) {
            if(page) {
                this.pagination.total = page.count;
            }
        },
        // 
        reset() {
            this.search = {
                pageNo: 1,
                pageSize: 10,
                name: '',
                status: ''
            }
            this.list();
        },
        // 
        list(_event, pageNo, pageSize) {
            let _pageNo = 1;
            if(pageNo) _pageNo = pageNo;
            this.search.pageNo = _pageNo;
            let _pageSize = 10;
            if(pageSize) _pageSize = pageSize;
            this.search.pageSize = _pageSize;

            this.$request.post('/api/wiseqr/prod/brands', this.search, true, (res)=>{
                if (res.ret == '200000') {
                    this.tableList = res.data.list || [];
                    this.initPagination(res.data.page||{});
                }
            });
        },
        dataForm(index, row) {
            this.form.id = "";
            this.form.name = "";
            if(row&&row.id) {
                this.form.id = row.id;
                this.form.name = row.name;
            }
            this.form.show = true;
        },
        saveForm() {
            this.$request.post('/api/wiseqr/prod/brand/saveOrModify', this.form, true, (res)=>{
                if (res.ret == '200000') {
                    this.list();
                    this.form.show = false;
                    this.$message({type: 'success', message: '!'});
                } else {
                    this.$message.error(res.message);
                }
            });
        },
        // status=1status=0
        modifyData(id, status) {
            let params = {id:id, status:status}
            this.$request.post('/api/wiseqr/prod/brand/mstatus', params, true, (res)=>{
                if (res.ret == '200000') {
                    this.list();
                    this.$message({type: 'success', message: '!'});
                } else {
                    this.$message.error(res.message);
                }
            });
        }
    }
}
</script>

<style scoped>
    .space {position:relative;width:100%;height:20px;}
    .el-input, .el-select {
        width: 200px;
    }
</style>



