<template>
  <div>
    <el-card class="box-card">
      <el-row>
        <el-button size="small" type="primary" @click="materielForm"></el-button>
      </el-row>
      <div class="space"></div>
      <el-tabs type="border-card" @tab-click="changeTab">
        <el-tab-pane label="">
          <!--  -->
          <el-form :inline="true" :model="form" class="demo-form-inline">
            <el-form-item label="">
              <el-input size="small" v-model="form.keywords" :placeholder="materielName[metraFlag]+''"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select size="small" v-model="supplierSel" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option v-for="item in supplierList" :key="item.supplierCode" :label="item.name" :value="item.supplierCode"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select size="small" v-model="form.status" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option v-for="item in statusList" :key="item.code" :label="item.name" :value="item.code"></el-option>
              </el-select>
            </el-form-item>
            <div></div>
            <el-form-item>
              <el-button size="small" type="primary" @click="listMateriel"></el-button>
              <el-button size="small" @click="resetForm"></el-button>
            </el-form-item>
          </el-form>
          <!--  -->
          <el-table :data="materielDatas" style="width: 100%">
            <el-table-column label="" type="index" align="center">
              <template slot-scope="scope">
                {{ (form.pageNo-1)*form.pageSize + scope.$index + 1 }}
              </template>
            </el-table-column>
            <el-table-column prop="name" :label="materielName[metraFlag]+''" align="center" width="180"></el-table-column>
            <el-table-column label="" align="center">
              <template slot-scope="scope">
                <img v-if="scope.row.pic" :src="scope.row.pic" />
              </template>
            </el-table-column>
            <el-table-column prop="supplierName" label="" align="center"></el-table-column>
            <el-table-column prop="stock" :label="'('+materielUnit[metraFlag]+')'" align="center"></el-table-column>
            <el-table-column prop="status" label="" align="center">
              <template slot-scope="scope">
                {{scope.row.status==1?"":""}}
              </template>
            </el-table-column>
            <el-table-column label="" align="center" width="220">
              <template slot-scope="scope">
                <el-button v-if="scope.row.status==1" size="mini" @click="materielForm(scope.$index, scope.row)"></el-button>
                <el-button v-if="scope.row.status==1" size="mini" @click="handleAddPool(scope.$index, scope.row)"></el-button>
                <el-button v-if="scope.row.status==1" size="mini" @click="handleDelete(scope.$index, scope.row)" type="danger"></el-button>
                <el-button v-if="scope.row.status==0" size="mini" @click="modifyData(scope.row.id, 1)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <div class="space"></div>
          <!--  -->
          <el-pagination :small="false" background layout="total, prev, pager, next, jumper"
            @size-change="sizeChange" @current-change="currentChange"
            :current-page="form.pageNo" :page-size="form.pageSize"
            :total="pagination.total">
          </el-pagination>
        </el-tab-pane>

        <el-tab-pane label="">
          <!--  -->
          <el-form :inline="true" :model="form" class="demo-form-inline">
            <el-form-item label="">
              <el-input size="small" v-model="form.keywords" :placeholder="materielName[metraFlag]+''"></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-select size="small" v-model="supplierSel" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option v-for="item in supplierList" :key="item.supplierCode" :label="item.name" :value="item.supplierCode"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="">
              <el-select size="small" v-model="form.operType" placeholder="">
                <el-option label="" value=""></el-option>
                <el-option v-for="(label, value) in operTypeList" :key="value" :label="label" :value="value"></el-option>
              </el-select>
            </el-form-item>
            <div></div>
            <el-form-item>
              <el-button size="small" type="primary" @click="listOperLog"></el-button>
              <el-button size="small" @click="resetForm"></el-button>
            </el-form-item>
          </el-form>
          <!--  -->
          <el-table :data="operLogDatas" style="width: 100%">
            <el-table-column label="" type="index" align="center">
              <template slot-scope="scope">
                {{ (form.pageNo-1)*form.pageSize + scope.$index + 1 }}
              </template>
            </el-table-column>
            <el-table-column prop="poolName" :label="materielName[metraFlag]+''" align="center" width="180"></el-table-column>
            <el-table-column prop="supplierName" label="" align="center"></el-table-column>
            <el-table-column prop="operName" label="" align="center"></el-table-column>
            <el-table-column prop="actName" label="" align="center"></el-table-column>
            <el-table-column prop="operNum" :label="'('+materielUnit[metraFlag]+')'" align="center"></el-table-column>
            <el-table-column label="" align="center" width="160">
              <template slot-scope="scope">
                {{new Date(scope.row.ctime).Format("yyyy-MM-dd hh:mm:ss")}}
              </template>
            </el-table-column>
            <el-table-column prop="creatorName" label="" align="center"></el-table-column>
          </el-table>
          <div class="space"></div>
          <!--  -->
          <el-pagination background layout="total, prev, pager, next, jumper"
            @size-change="sizeChange" @current-change="currentChange"
            :current-page="form.pageNo" :page-size="form.pageSize"
            :total="pagination.total">
          </el-pagination>
        </el-tab-pane>
      </el-tabs>
    </el-card>
    <el-dialog title="" width="30%" :visible.sync="addPool.show">
      <el-form ref="addPool" label-width="100px">
        <el-form-item label="" prop="stock">
          <el-input size="small" type="number" v-model="addPool.num"></el-input>
        </el-form-item>
        <!--  -->
        <el-form-item v-if="metraFlag=='virtual'" label="">
          <el-upload size="small" class="upload-demo"
            action="/api/wiseqr/metra/import"
            :headers="headers"
            :data="addPool"
            :before-upload="handlerSourceFileBUpload"
            :on-success="handleSourceFileSuccess"
            :on-remove="handleSourceFileRemove"
            :file-list="sourceFiles">
            <el-button size="small" type="primary"></el-button>
            <div slot="tip" class="el-upload__tip">csv/excel</div>
          </el-upload>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button size="small" @click="addPool.show=false"> </el-button>
        <el-button size="small" type="primary" @click="confirmAddPool"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Config from '@/pages/Product/datas/conf'
export default {
  props: ['metraFlag'],
  data() {
    return {
      materielName: Config.materielName,
      materielUnit: Config.materielUnit,
      headers: {
        "token": sessionStorage.getItem("access_token"),
        "loginId": sessionStorage.getItem("access_loginId")
      },
      tabIdx: 0, // 
      supplierList: [],
      statusList: [],
      operTypeList: {}, // key:value
      supplierSel: '', // 
      form: {
        metraFlag: this.metraFlag,
        pageNo: 1,
        pageSize: 10,
        keywords: '', // 
        supplierArr: [], // 
        status: '', // 
        operType: '' // 
      },
      pagination: { // 
        total: 0
      },
      materielDatas: [], // 
      operLogDatas: [],  // 

      addPool: {
        show: false, // 
        id: '', // id
        num: '', // 
        type: '',  // 
      },
      sourceFiles: [] // {name:'', sourceCode: ''}
    }
  },
  created() {
    this.listMateriel();
    this.getSuppliers();
    this.getStatus();
    this.getOperTypes();
  },
  watch: {
    // 
    supplierSel(nval, oval) {
      if(nval!=oval) {
        this.form.supplierArr = [];
        if(nval) {
          this.form.supplierArr[0] = nval;
        }
      }
    }
  },
  methods: {
    currentChange(pageNo) {
      // pageNo
      if(this.tabIdx==0) {
        this.listMateriel(event, pageNo);
      } else if(this.tabIdx==1) {
        this.listOperLog(event, pageNo);
      }
    },
    sizeChange(pageSize) {
      // pageNo
      if(this.tabIdx==0) {
        this.listMateriel(event, null, pageSize);
      } else if(this.tabIdx==1) {
        this.listOperLog(event, null, pageSize);
      }
    },
    // 
    getSuppliers() {
      this.$request.post('/api/wiseqr/supplier/list', {pageSize:-1, status: 1}, true, (res)=>{
        if (res.ret == '200000') {
          this.supplierList = res.data.list||[];
        }
      });
    },
    // 
    getStatus() {
      this.$request.post('/api/wiseqr/dim/dataStatus', {}, false, (res)=>{
        if (res.ret == '200000') {
          this.statusList = res.data||[];
        }
      });
    },
    // 
    getOperTypes() {
      this.$request.post('/api/wiseqr/metra/operTypes', {}, false, (res)=>{
        if (res.ret == '200000') {
          this.operTypeList = res.data||{};
        }
      });
    },
    // 
    resetForm() {
      this.supplierSel = '';
      this.form = {
        metraFlag: this.metraFlag,
        pageNo: 1,
        pageSize: 10,
        keywords: '', // 
        supplierArr: [], // 
        status: '', // 
        operType: '' // 
      }
      this.currentChange(1);
    },
    // tab
    changeTab(tab) {
      this.tabIdx = tab.index;
      if(tab.index==0) {
        this.listMateriel();
      } else if(tab.index==1) {
        this.listOperLog();
      }
    },
    // 
    materielForm(index, row) {
      //let _path = "/product/materiel/form?metraFlag="+this.metraFlag;
      //if( row&&row.id ) {
      //  _path += "&id="+row.id;
      //}
      //this.$router.push({path:_path});
      let materielId = "";
      if(row&&row.id) {
        materielId = row.id;
      }
      this.$emit('showForm', materielId);
    },
    // 
    listMateriel(_event, pageNo, pageSize) {
      let _pageNo = 1;
      if(pageNo) _pageNo = pageNo;
      this.form.pageNo = _pageNo;
      let _pageSize = 10;
      if(pageSize) _pageSize = pageSize;
      this.form.pageSize = _pageSize;
      
      this.$request.post('/api/wiseqr/metra/list', this.form, true, (res)=>{
        if (res.ret == '200000') {
          this.materielDatas = res.data.list || [];
          this.initPagination(res.data.page||{});
        }
      });
    },
    // 
    listOperLog(_event, pageNo, pageSize) {
      if(pageNo) {
        this.form.pageNo = pageNo;
      } else {
        this.form.pageNo = 1;
      }
      if(pageSize) {
        this.form.pageSize = pageSize;
      } else {
        this.form.pageSize = 10;
      }
      this.$request.post('/api/wiseqr/metra/operList', this.form, true, (res)=>{
        if (res.ret == '200000') {
          this.operLogDatas = res.data.list || [];
          this.initPagination(res.data.page||{});
        }
      });
    },
    // page = {"pageCount":, "count":}
    initPagination(page) {
      if(page) {
        this.pagination.total = page.count;
      }
    },
    // confirm
    handleDelete(index, row) {
      this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
          center: true
        }).then(() => {
          this.modifyData(row.id, 0);// 
        }).catch(() => {/**  */});
    },
    // status=1status=0
    modifyData(id, status) {
      let params = {metraFlag:this.metraFlag, id:id, status:status}
      this.$request.post('/api/wiseqr/metra/modifyStatus', params, true, (res)=>{
        if (res.ret == '200000') {
          this.listMateriel();
          this.$message({type: 'success', message: '!'});
        } else {
          this.$message.error(res.message);
        }
      });
    },
    // 
    handleAddPool(index, row) {
      this.addPool.id = row.id;
      this.addPool.type = row.type;
      this.addPool.show = true;
    },
    // 
    handlerSourceFileBUpload(file) {
    },
    // 
    handleSourceFileSuccess(res, file) {
      if(res.ret==200000) {
        //sourceCode: "D248BAE8A6DE47168F2BA5C1B1E2B3EE"
        //sourceFile: ".xlsx"
        //successCount: 2
        //successMsg: "2"
        this.$message({type:'success', message:res.data.successMsg});
        let obj = {name:res.data.sourceFile, sourceCode:res.data.sourceCode, count:res.data.successCount};
        this.sourceFiles.push(obj);
      } else {
        this.$message.error(res.message);
      }
    },
    // 
    handleSourceFileRemove(file, fileList) {
      this.sourceFiles = []; // 
    },
    confirmAddPool() {
      if(!this.addPool.num) {
        this.$message.error("0");
        return false;
      }
      let params = {metraFlag:this.metraFlag, id:this.addPool.id, increment:this.addPool.num};
      if(this.metraFlag=='virtual'&&this.addPool.type=='890') { // 
        if(this.sourceFiles.length==0) { // 
          this.$message.error("");
          return false;
        }
        let obj = this.sourceFiles[0];
        const count = obj.count;
        if(count!=this.addPool.num) {// 
          this.$confirm('', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            params.sourceCode = obj.sourceCode;
            params.sourceFile = obj.name;
            this.requestAddStore(params);
          }).catch(() => {
            //this.addPool.show = false;
          });
          return false;
        }
      }
      this.requestAddStore(params);
    },
    requestAddStore(params) {
      this.$request.post('/api/wiseqr/metra/addStore', params, true, (res)=>{
        if (res.ret == '200000') {
          this.addPool.id = '';
          this.addPool.num = '';
          this.addPool.type = '';
          this.addPool.show = false;
          this.listMateriel();
        } else {
          this.$message.error(res.message);
        }
      });
    }
  }
}
</script>

<style scoped>
  .space {position:relative;width:100%;height:20px;}
  .el-input, .el-select, .el-upload-list {
    width: 200px;
  }
  .el-table img {
    width: 80px;
    height: 80px;
  }
</style>



